<template>
  <div id="qeditor">
    <quill-editor
      :key="custom"
      ref="qeditor"
      v-model="content"
      :options="editorOption"
      @change="onEditorChange($event)"
    />
  </div>
</template>
<script>
import $ from "jquery";
import "./index.scss";
import Quill from "quill"; // 引入编辑器
import { quillEditor } from "vue-quill-editor";
import html_lens from "./html_lens";
import html_stick from "./html_stick";
import {Delta,Size,Font} from './register'

const markIcon = require("../../assets/center.svg");

export default {
  name: "qeditor",
  props: {
    custom: String,
  },
  components: {
    quillEditor,
  },
  created() {
    this.editorOption.modules.toolbar.container[14] = [
      { RN1: ["c", "l", "r"] },
    ];
  },
  mounted() {
    this.setHtml();
  },
  updated(){
        //   const _this=this
    this.$refs.qeditor.quill.on(
      "text-change",
      function (delta, oldDelta, source) {
        if (source == "api") {
        //   console.log("An API call triggered this change.");
        } else if (source == "user") {
            // console.log('delta,oldDelta :>> ', delta,oldDelta);
            // console.log('22', _this.$refs.qeditor.quill.getContents());
        //   console.log("A user action triggered this change.");
        }
      }
    );
  },

  data() {
    return {
      content: ``,
      editorOption: {
        modules: {
          toolbar: {
            container: [
              ["bold", "italic", "underline", "strike"], // 加粗 斜体 下划线 删除线 -----['bold', 'italic', 'underline', 'strike']
              ["blockquote", "code-block"], // 引用  代码块-----['blockquote', 'code-block']
              [{ list: "ordered" }, { list: "bullet" }], // 有序、无序列表-----[{ list: 'ordered' }, { list: 'bullet' }]
              [{ header: 1 }, { header: 2 }],
              [{ script: "sub" }, { script: "super" }], // 上标/下标-----[{ script: 'sub' }, { script: 'super' }]
              [{ indent: "-1" }, { indent: "+1" }],
              [{ size: Size.whitelist }], // 字体大小
              [{ header: [1, 2, 3, 4, 5, 6, false] }], // 标题-----[{ header: [1, 2, 3, 4, 5, 6, false] }]
              [
                { color: ["#fff", "#969799", "#d1120d", "#000"] },
                { background: ["#fff", "#969799", "#d1120d", "#000"] },
              ], // 字体颜色、字体背景颜色-----[{ color: [] }, { background: [] }]
              ["clean"], // 清除文本格式-----['clean']

              [{ font: Font.whitelist }], //显示字体选择
              [{ align: [] }], // 对齐方式-----[{ align: [] }]
              ["image"], // 链接、图片、视频-----['link', 'image', 'video']
              //   ['RN2']

              //工具栏设置下拉菜单
            ],

            handlers: {
              that: this,
              update: this.emitHtml,
              RN1: function (v) {
                $('[class^="ql-RN1-"]').remove()
                $('#rn-box').remove()
                const pString = `
                  <p class="ql-RN1-${v}">
                    <span style="font-family:Asul;font-size: 22px;color: rgb(150, 151, 153);" >Room No. </span>
                    <span style="background: #fff; height: 33px; border-radius:4px;">___________________</span>
                    &nbsp;
                  </p>
                `;
                let value = this.quill.clipboard.convert(pString); //把html转换成delta格式
                let range = this.quill.getSelection(true);
                const delta = new Delta()
                  .retain(range.index)
                  .delete(range.length)
                  .insert('\n')
                  .concat(value)
                  .insert("\n");
                this.quill.updateContents(delta, Quill.sources.USER);
                $(`.ql-RN1-${v}`).attr('id','rn')
                $(`.ql-RN1-${v} span:nth-child(2)`).attr({
                    id:'rn-box',
                    'contenteditable':false
                })
                this.content = this.handlers.update();
              },
              RN2: function () {
                $("#rn").remove();
                $(".ql-RN2-c").remove();
                
                // 把需要的style样式注册这样转化防止成delta时被过滤
                const pString = `<p class="ql-RN2-c ql-align-center">
                <span style="font-size: 22px; font-family: Asul;" >Room No. [</span>
                <span style="font-size: 22px; font-family: Asul; background-color: rgb(255, 255, 255); color: rgb(0, 0, 0);">001</span>
                <span style="font-size: 22px; font-family: Asul;" >]</span></p>`;

                let value = this.quill.clipboard.convert(pString); //把html转换成delta格式
                let range = this.quill.getSelection(true);
                const delta = new Delta()
                  .retain(range.index)
                  .delete(range.length)
                  .insert("\n")
                  .concat(value)
                  .insert("\n");
                if (delta.ops[2].insert === "001") {
                  delta.ops[2].insert = "  001  ";
                } else {
                  delta.ops[3].insert = "  001  ";
                }
                this.quill.updateContents(delta, Quill.sources.USER);
                $(`.ql-RN2-c`).attr('id','rn')
                this.content = this.handlers.update();
              },
              image: function () {
                var _this3 = this.quill.getModule("toolbar");
                var fileInput = _this3.container.querySelector(
                  "input.ql-image[type=file]"
                );
                if (fileInput == null) {
                  fileInput = document.createElement("input");
                  fileInput.setAttribute("type", "file");
                  fileInput.setAttribute(
                    "accept",
                    " image/svg+xml, image/png, image/jpeg, image/bmp, image/x-icon"
                  );
                  fileInput.classList.add("ql-image");
                  fileInput.addEventListener("change", function () {
                    if (fileInput.files != null && fileInput.files[0] != null) {
                      var reader = new FileReader();
                      reader.onload = function (e) {
                        var range = _this3.quill.getSelection(true);
                        _this3.quill.updateContents(
                          new Delta()
                            .retain(range.index)
                            .delete(range.length)
                            .insert({ image: e.target.result })
                        );
                        _this3.quill.setSelection(range.index + 1);
                        fileInput.value = "";
                      };
                      reader.readAsDataURL(fileInput.files[0]);
                    }
                  });
                  _this3.container.appendChild(fileInput);
                }
                fileInput.click();
              },
            },
          },

          //   调整图片大小
          imageResize: {
            displayStyles: {
              backgroundColor: "black",
              border: "none",
              color: "white",
            },
            modules: ["Resize"],
          },
        },
      },
    };
  },

  methods: {

    emitHtml() {
      const html = document
        .querySelector(".ql-editor")
        .innerHTML.replace("[", "")
        .replace("]", "");
      this.$emit("editor", html);
    },
    onEditorChange({ html }) {
      $('[class^="ql-RN1-"]').css('display',"flex"); // 直接在css中flex会导致换行，暂不清楚原因后面再研究
      this.content = html.replace('<p id="rn"><br></p>', "");
      this.$emit(
        "editor",
        html
          .replace('<p id="rn"><br></p>', "")
          .replace("[", "")
          .replace("]", "")
      );
    },
    /* 设置编辑器的默认内容 */
    setHtml(v) {
      let html = "";
      let defHtml = "";
      if (this.custom === "lens") {
        html = localStorage.getItem("html_lens");
        defHtml = html_lens;
      } else {
        html = localStorage.getItem("html_stick");
        defHtml = html_stick;
      }
      this.$refs.qeditor.$el.querySelector(".ql-editor").innerHTML = "";
      this.$refs.qeditor.$el.querySelector(".ql-editor").innerHTML =
        v === 1 ? defHtml : html || defHtml;
      $('[class^="ql-RN1-"]').css('display',"flex"); // 直接在css中flex会导致换行，暂不清楚原因后面再研究
      
    },
  },
  watch: {
    custom: {
      handler(v) {
        this.$nextTick(() => {
          this.setHtml();
          this.editorOption.modules.toolbar.container[14] =
            v === "lens" ? ["RN2"] : [{ RN1: ["c", "l", "r"] }];
          const RN2 = $(".ql-RN2")[0];
          if (RN2) {
            const img = document.createElement("img");
            img.width = 22;
            img.height = 22;
            img.src = markIcon;
            RN2.appendChild(img);
          }
        });
        // 删除整块
        this.$nextTick(() => {
          this.$refs.qeditor.$el.addEventListener("keydown", (e) => {
            if (e.keyCode !== 8) {
              return false;
            }
            const maybeRnBox = window.getSelection().focusNode.parentNode;
            if (
              maybeRnBox.getAttribute("contenteditable") === "false" &&
              (maybeRnBox.id === "rn-box" || maybeRnBox.className === "rn-box")
            ) {
              maybeRnBox.parentNode.remove();
            }
          });
        });
      },
      immediate: true,
    },
  },
};
</script>

<style lang="scss">
@import "./index.scss";
</style>
