// 注册插件或改变一些Quill的配置

import Quill from "quill"; // 引入编辑器
import ImageResize from "quill-image-resize-module";
export const Delta = Quill.import("delta");

/* 注册图片插件 */

Quill.register("modules/imageResize", ImageResize);

/* 自定义字体大小******************** */
export const Size = Quill.import("attributors/style/size");
Size.whitelist = [
  "12px",
  "14px",
  "16px",
  "18px",
  "20px",
  "22px",
  "24px",
  "36px",
  "48px",
];
Quill.register(Size, true);

/* 自定义字体样式******************** */
export const Font = Quill.import("attributors/style/font");
Font.whitelist = ["Open Sans", "Anton", "Playfair", "Asul"]; // 将字体加入到白名单
Quill.register(Font, true);

/* 注册一种（style、class、、或见源码），使其可成为delta结构中ops的attribute属性*/
let Parchment = Quill.import("parchment");

export const height = new Parchment.Attributor.Style("height", "height", {
    scope: Parchment.Scope.INLINE, //匹配span
    whitelist: ["initial", "33px"],
});
Quill.register({ "formats/height": height }, true);

export const borderRadius = new Parchment.Attributor.Style("borderRadius","border-radius",{
    scope: Parchment.Scope.INLINE, //匹配span
    whitelist: ["initial", "4px"],
  }
);
Quill.register({ "formats/borderRadius": borderRadius }, true);


let config = {
    scope: Parchment.Scope.BLOCK,
    whitelist: ["l","c","r"]
  };

